
body {
  width: 100%;
  height: 100vh;
  background-color: #1D1D1D;
  overflow-x: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fff;
}
.title {
  color: white;
}
.logo {
  padding-top: 25px;
  width: 20%;
}
.back {
  background-color: #1D1D1D;
}
.back a {
  color: red;
}



